          </div>
          <div class="btns">
            <button id="buy" @click="shoppingCart.collectItemIds(book.id)">
              Buy Now
            </button>
            <button id="cart" @click="orderCollection.createWishList(book)">
              Add to Wishlist
            </button>
          </div>
        </div>
        <div class="all-informations">
          <h5>
