          </h6>

          <div class="current-price">
            <p>Best Price:</p>
            <p id="money">
              <b
                >{{ book.currentPrice }}
                <i class="fa-solid fa-indian-rupee-sign fa-xs"></i
              ></b>
            </p>
          </div>

          <div class="mrp">
            <p>
              MRP:
              <b
                >{{ book.mrp }}
                <i class="fa-solid fa-indian-rupee-sign fa-xs"></i
              ></b>
