          </div>

          <div class="description">
            <p>
              {{ book.bookDescription }}
            </p>
          </div>

          <div class="highlights">
            <ul>
              <li>
                <p>
                  <b>Publication Year:</b> <span>{{ book.pubYear }}</span>
                </p>
              </li>
              <li>
                <p><b>Language:</b></p>
                <span v-for="(v, i) in book.languageList" :key="i">{{
